.card__wrapper {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-flow: column;
  width: 200px;
  padding: 32px 0 16px;
  display: flex;
}

.card__image {
  width: 160px;
  margin: 0 auto 35px;
}

.card__image img {
  width: 100%;
}

.card__info, .card__actions {
  padding: 0 18px;
}

.card__name {
  color: #060b35;
  margin: 0 0 1px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__sku {
  color: #616070;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__stars {
  justify-content: space-between;
  align-items: center;
  margin: 16px 0 22px;
  display: flex;
}

.card__stars-sum {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 1.5;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__price-label {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.5;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.5;
}

.card__btn {
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  margin-top: 12px;
  padding: 9px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 20px;
}

.card__btn:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-repeat: no-repeat;
  background-size: 14px 14px;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin: 0;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

body {
  box-sizing: border-box;
  margin: 0;
  font-family: Roboto, Arial, Helvetica, sans-serif;
  font-style: normal;
}

*, :before, :after {
  box-sizing: border-box;
}
